/**
 * Maps RhinoScriptSyntax functions to their categories
 */

export function getFunctionCategory(functionName: string): string | null {
  if (functionName in FUNCTION_CATEGORY_MAP) {
    return FUNCTION_CATEGORY_MAP[functionName];
  }
  return null;
}

